<script>
  export default {
    data() {
        return {
            count: [],
            orders: [],
            search: '',
            searchResult: [],
            modals: {
                search: false,
                address: false
            },
            options: [{
                value: 'pending',
                label: ''
            },
            {
                value: 'paid',
                label: '' 
            }],
            print: {
                phone: '',
                price: 0,
                status: 'pending',
                name: '',
                district: '',
                khoroo: '',
                address: ''
            },
            delivered: {
                pagination: 0,
                page: 1,
                list: []
            }
        }
    },
    mounted() {
      this.getOrders();
    },
    methods: {
        printAddress() {
            this.$htmlToPaper('printMe');
        },
        showOrder(ordernumber) {
            this.$router.push({ name: ' ', params: {id: ordernumber}})
        },
        searchOrder() {
            this.searchResult = [];
            if(this.search != '') {
                var rts = this;
                    var token = localStorage.getItem('token');
                    rts.$axios({
                    method: 'POST',
                    url: rts.$appUrl + '/api/admin/search-order',
                    headers: {
                        "Authorization": `Bearer ${token}`
                    },
                    data: {
                        ordernumber: this.search
                    }
                }).then(data => {
                    if (data.data.result == 'failed') {
                        rts.$notify({
                            title: '',
                            message: ' ',
                            type: 'warning'
                        });
                    } else {
                        rts.searchResult = data.data.order;
                        rts.modals.search = true;
                    }
                });
            }
        },
        selectOrder(status) {
            var val = [];
            this.orders.forEach(el => {
                if(el.status == status) {
                    val.push(el)
                }
            });
            return val;
        },
        selectCount(st) {
            var val = 0;
            this.count.forEach(el => {
                if(el.status == st) {
                    val =  el.count;
                }
            });
            return val;
        },
        getOrders() {
            var rts = this;
            var token = localStorage.getItem('token');
            rts.$axios({
              method: 'POST',
              url: rts.$appUrl + '/api/admin/orders',
              headers: {
                  "Authorization": `Bearer ${token}`
              }
          }).then(data => {
              rts.count = data.data.count;
              rts.orders = data.data.orders;
              data.data.orders.forEach(el => {
                  if(el.status == 'delivered') {
                      rts.delivered.list.push(el);
                  }
              });

            let sc = data.data.orders.length % 10;
            let f = parseInt(data.data.orders.length / 10);
            if(sc > 0) {
                f += 1;
            }
            rts.delivered.pagination = f;
          });
        }
    }
  }
</script>
